/* Colors */
@value active: #cd8b76;
@value dark: #183642;
@value border: #ffff00;
@value disabled: #6d435a;
@value white: #ffffff;
@value selected: #7c9082;

@font-face {
  font-family: 'Darker Grotesque', sans-serif;
  src: url('https://fonts.googleapis.com/css2?family=Darker+Grotesque:wght@300&display=swap');
}

.App {
  text-align: center;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: #c8c8c8;
}



.btn {
  margin-top: 1rem;
  padding: 10px 40px;
  background: dark;
  color: white;
  text-transform: uppercase;
  border: none;
  border-radius: 10px;
}



.container {
  max-width: 80%;
  border-radius: 10px;
  margin: auto;
}


.nav {
  list-style-type: none;
  width: 500px;
  display: flex;
  justify-content: space-evenly;
}


.link {
  padding: 5px 10px;
  color: border;
  text-decoration: none;
  background: none;
  border: none;
  border-bottom: 2px solid active;
}

.heading {
  margin-bottom: 30px;
  color: black;
  font-weight: 600;
  font-size: 32px;
  font-family: 'Darker Grotesque', sans-serif;
}

.title {
  color: dark;
  font-weight: 600;
  font-size: 4rem;
  font-family: 'Darker Grotesque', sans-serif;
  line-height: 12px;
  display: flex;
  justify-content: center;
  font-weight: lighter;
}

.subTitle {
  color: disabled;
  font-weight: normal;
  font-size: 16px;
  font-family: 'SF Text', sans-serif;
  line-height: 14px;
}

.box {
  background-color: white;
  border: 1px solid border;
  border-radius: 10px;
}

.markdown {
  color: black;
  font-size: 18px;
}

.markdown h1 {
  font-weight: bold;
  font-size: 26px;
}
.markdown h2 {
  font-weight: bold;
  font-size: 24px;
}
.markdown h3 {
  font-weight: bold;
  font-size: 20px;
}
.markdown h4 {
  font-weight: bold;
  font-size: 18px;
}

.markdown ul {
  padding-left: 20px;
  list-style: square;
}

.markdown ol {
  padding-left: 20px;
}

.markdown a {
  color: active;
}

.input:focus {
  border-color: active;
}
.input::placeholder {
  color: disabled;
}

.logo {
  height: 10rem;
  width: 50%;
  border: none;
}

.photo {
  height: 20rem;
  width: 15rem;
  justify-content: space-between;
  padding: 25px;
}

.pageContent {
  margin-top: 20rem;
  width: 85%;
}

.banner {
  border-bottom: 2px solid selected;
  /* margin-top: 20rem; */
}

.homeContent {
  display: flex;
  width: 90%;
  
}

h2 {
  font-size: 1.3rem;

}

.words {
  padding: 15px;
  margin: 10px;
}
